<!-- 票务分销渠道分析--扇形图 -->
<template>
  <div id="seD"
       style="width:100%;height:100%"></div>
</template>

<script>
let myChart;
export default {
  name: "trAChart",
  props: ['title', 'option'],
  data () {
    //这里存放数据
    return {
      myOption: {}
    };
  },
  watch: {
    option (n, o) {
      this.myOption = n
      this.setOption()
    }
  },
  created () {
    this.myOption = this.option
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.init()
  },
  //方法集合
  methods: {
    init () {
      myChart = this.$echarts.init(document.getElementById('seD'));

      // 指定图表的配置项和数据
      var option = {
        title: {
          text: this.title,
          left: '5%',
          top: '10',
          textStyle: {
            fontSize: 17,
          },
        },
        tooltip: {
          trigger: 'item',
          formatter: function (a) {
            let name = ''
            if (a.name == '北京锦绣神州旅行社有限公司') {
              name = '锦绣神州'
            } else if (a.name == '官方小程序') {
              name = '小程序'
            } else if (a.name == '农商行市民卡') {
              name = '市民卡'
            } else if (a.name == '世园会票务预定中心') {
              name = '票务预定'
            } else if (a.name == '同程网络科技股份有限公司') {
              name = '同程网络'
            } else if (a.name == '我的扬州APP') {
              name = '我的扬州'
            } else if (a.name == '现场售票') {
              name = '现场售票'
            } else if (a.name == '扬州江都中原国际旅行社有限公司') {
              name = '江都中原'
            }
            return a.marker + name + "   " + a.value + "%"
          }
        },
        legend: {
          type: "scroll",
          orient: 'vertical',
          itemHeight: 12, // 设置高度
          align: 'left',
          right: 10,      //可设定图例在左、右、居中
          top: 'center',
          textStyle: {
            fontSize: 12,
            color: "#333333",
          },
          formatter: function (name) {
            if (name == '北京锦绣神州旅行社有限公司') {
              return '锦绣神州'
            } else if (name == '官方小程序') {
              return '小程序'
            } else if (name == '农商行市民卡') {
              return '市民卡'
            } else if (name == '世园会票务预定中心') {
              return '票务预定'
            } else if (name == '同程网络科技股份有限公司') {
              return '同程网络'
            } else if (name == '我的扬州APP') {
              return '我的扬州'
            } else if (name == '现场售票') {
              return '现场售票'
            } else if (name == '扬州江都中原国际旅行社有限公司') {
              return '江都中原'
            }
          }
        },
        grid: {
          // containLabel: true,
        },
        series: [{
          type: 'pie',
          radius: ['30%', '60%'],
          center: ['30%', '50%'],
          // avoidLabelOverlap: false,
          //指示线去掉
          label: {
            normal: {
              position: 'inner',
              show: true,
              textStyle: {
                fontWeight: 400,
                fontSize: 11.5,
                color: '#fff',  //文字的字体大小
              },
              formatter: '{d}%'
            },
          },
        }],
        emphasis: {
          label: {
            show: true,
            fontSize: '14',
          }
        },

      };
      myChart.setOption(option);
    },
    setOption () {
      myChart.setOption(this.myOption);
    }
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
#trA {
  background-color: blueviolet;
}
</style>