<!-- 景区客流统计柱状图 -->
<template>
  <div id="trA"
       style="width:100%;height:100%"></div>
</template>

<script>
export default {
  name: "trAChart",
  props: ['title', 'option'],
  data () {
    //这里存放数据
    return {
      myOption: {},
      myChart: null
    };
  },
  watch: {
    option (n) {
      this.myOption = n
      this.setOption()
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.myOption = this.option
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.init()
  },
  //生命周期 - 更新之后
  updated () {
    this.init()
  },

  //方法集合
  methods: {
    init () {
      this.myChart = this.$echarts.init(document.getElementById('trA'));

      // 指定图表的配置项和数据
      var option = {
        title: {
          text: this.title,
          left: '5%',
          top: '10',
          textStyle: {
            fontSize: 17,
          },
        },
        grid: {
          left: 4, // 默认10%，给24就挺合适的。
          top: 80, // 默认60
          right: 14, // 默认10%
          bottom: 10, // 默认60
          containLabel: true
        },
        legend: {
          show: true,
          data: ['收入'],
          top: 30,
          right: 10,
          textStyle: {
            fontSize: 12,
            color: "#333333",
          },
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
          },
          formatter: function (a) {
            let yuan = Number(a[0].value)
            if (yuan > 10000) {
              yuan = (yuan / 10000).toFixed(2) + ' 万元'
            } else {
              yuan = yuan + ' 元'
            }
            return a[0].marker + "收入  " + yuan
          }
        },
        xAxis: {
          type: 'category',
          axisTick: {
            alignWithLabel: true
          },
        },
        yAxis: {
          type: 'value',
          axisLabel: {
            formatter: function (value) {
              return value / 10000 + '万元';
            }
          }
        },
        series: {
          type: 'bar',
          name: '收入',
          itemStyle: {
            color: '#5870F8'
          },
        }
      };

      // 使用刚指定的配置项和数据显示图表。
      this.myChart.setOption(option);
    },
    setOption () {
      this.myChart.setOption(this.myOption);
    }
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
</style>