<!-- 报表统计--票务数据分析 -->
<template>
  <van-row style="height:100%">
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">票务数据分析</p>
      </van-col>
    </van-row>
    <van-row class="dt-content">
      <van-row class="dt-income">
        <van-row class="dt-income-title">今日收入</van-row>
        <van-row class="dt-income-number">{{yuan}}
        </van-row>
      </van-row>
      <div class="dt-chart-box">
        <ticketBar title="票务收入分析（近七日）"
                   :option="option1"></ticketBar>
      </div>
      <div class="dt-chart-box">
        <sectorDiagram title="票务分销渠道分析（本周）"
                       :option="option2">
        </sectorDiagram>
      </div>
    </van-row>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import ticketBar from "@/components/echarts/ticketBar";
import sectorDiagram from "@/components/echarts/sectorDiagram";
import {
  ticketChannel,
  tourismIncome
} from "@/api/chart";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    ticketBar,
    sectorDiagram,
  },
  data () {
    //这里存放数据
    return {
      yuan: 0,
      option1: {},
      option2: {},
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getTourismIncome()
    this.getTicketChannel()
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {

  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    getTicketChannel () {
      ticketChannel().then(res => {
        let dataList = res.data
        dataList.sort((a, b) => {
          if (a.nums > b.nums) {
            return -1
          } else {
            return 1
          }
        })
        let myData = []
        let legendData = []
        for (const item of dataList) {
          myData.push({
            name: item.channelName,
            value: item.percent
          })
          legendData.push(item.property)
        }
        this.option2 = {
          legendData: legendData,
          series: {
            data: myData,
          }
        }
      })
    },
    //票务收入分析
    getTourismIncome () {
      tourismIncome('day').then(res => {
        let dataList = res.data
        dataList = dataList.slice(dataList.length - 7, dataList.length)
        let xData = []
        let seriesData = []
        dataList.forEach(item => {
          xData.push(item.createTime)
          seriesData.push(item.ticketIncome)
        });
        let yuan = dataList[dataList.length - 1].ticketIncome
        if (yuan > 10000) {
          this.yuan = (yuan / 10000).toFixed(2) + ' 万元'
        } else {
          this.yuan = yuan + ' 元'
        }
        this.option1 = {
          xAxis: {
            data: xData
          },
          series: [
            {
              data: seriesData
            }
          ]
        }
      })
    },

    // 返回首页按钮
    Home () {
      this.$router.push({ name: 'Home' })
    },
    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../../styles/dimens';
$noticeBarHeight: px2rem(50);
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: 'SourceHanSansCN-Medium';
  color: #ecedf2;
}
.dt-content {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: calc(100vh - #{$headerHeight} - #{$statusBarHeight});
  background-color: #edf1f5;
  overflow-y: auto;
  .dt-income {
    width: 95%;
    height: px2rem(86.5);
    background-image: url('../../../assets/img/今日收入.png');
    background-size: 100% px2rem(116.5);
    background-repeat: no-repeat;
    margin-top: px2rem(11.5);
    padding-top: px2rem(30.5);
    .dt-income-title {
      text-align: center;
      font-size: px2rem(17);
      font-family: ' PingFang-SC-Medium';
      color: #ffffff;
      font-weight: 500;
      text-align: center;
    }
    .dt-income-number {
      text-align: center;
      margin-top: px2rem(24.5);
      margin-bottom: px2rem(20);
      font-size: px2rem(14.5);
      font-family: ' PingFang-SC-Medium';
      color: #ffffff;
      font-weight: 400;
    }
  }
  .dt-chart-box {
    width: 95%;
    height: px2rem(250);
    margin-top: px2rem(10);
    background: #ffffff;
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    border-radius: 8px;
  }
}
</style>